<template>
	<keep-alive>
		<div class="chatpage">
			
			<!-- 头部区 -->
			<div class="chatpagehead">
				<i @click="$store.commit('hiddenChatPage',chatPageMsg.weChatId);" class="chatpagereturn icon-chevron-left"></i>
				<span class="chatpagename" v-html="chatPageMsg.name ? chatPageMsg.name : chatPageMsg.nickname"></span>
				<!-- 聊天页面中右上角三个点的页面 -->
				<span class="contactsmore" @click="$store.commit('showOrHidden',['chatPageMore',true])">...</span>
			</div>
			
			
			<div class="all" ref='all'>

				<!-- 消息区 -->
				<div class="information" :style="{'background':`url(${$store.state.chatPageMsg.ChatBackground})`}">

					<div class="messageitem" v-for="(v,i) in chatPageMsg.information" :key="i"  :class="v.self ? 'self' : 'other'" :id="i + 'i'">
						<!-- 头像，点击可进入联系人详情页面 -->
						<div class="headImg"  v-if="!v.self" @click.stop="$store.commit('showOrHidden',['inFoPage',true])">
							<img class="auto-img other" :src="chatPageMsg.headPortrait">
						</div>
						<div  class="onemsg" :id="v.informationlength">
							<div class="left" v-if="!v.self"></div>
							<span>{{v.msg}}</span>
							<div class="right" v-if="v.self"></div>
						</div>
						
						<div class="headImg" v-if="v.self">
							<img class="auto-img me"  :src="$store.state.myInfomation.headPortrait">
							<!-- <img class="auto-img me"  :src="require('../../public/img/' + $store.state.myInfomation.headPortrait + '.png')"> -->
						</div>
					</div>
					
				</div>
				
				<!-- 键盘区 -->
				<div class="chatpagekeyboard">
					<div style="display: flex;padding: 0.5rem 0;box-sizing: border-box;">
						<i class="record icon-video"></i>
						<input autofocus ref="chatpageinput" @keyup.enter="sendmsg" class="chatpageinput" @input="judgevalue">
						<i class="face icon-trash-2"></i>
						<span v-if="havevalue" class="chatpagesend" @click="sendmsg" >发送</span>
						<i v-else class="chatpagemore icon-plus"></i>
					</div>
				</div>
				
			</div>
			
			<!-- 右上角更多页面 -->
			<transition name="rightToLeft">
				<div class="ChatPageMore Page" v-show="$store.state.chatPageMore">
					<div class="chatpagehead">
						<i @click="$store.commit('showOrHidden',['chatPageMore',false]);" class="chatpagereturn icon-chevron-left"></i>
						<span class="chatpagename">聊天信息</span>
					</div>
					<div class="body">
						<div class="imgDiv">
							<div class="imgItem">
								<img class="auto-img" :src="chatPageMsg.headPortrait">
								<!-- <img class="auto-img" :src="require('../../public/img/' + chatPageMsg.headPortrait + '.png')"> -->
								<p class="name">{{chatPageMsg.name ? chatPageMsg.name : chatPageMsg.nickname}}</p>
							</div>
							<div class="imgItem">
								<span class="addFriend">
									<i class="vertical"></i><i class="horizontal"></i>
								</span>
							</div>
						</div>
						
						<div class="item" v-for="(v,i) in ChatPageMore" :key="i" :class="v.class" @click="allFun(i)">
							<div class="text">{{v.text}}</div>
							<!--顶置聊天-->
							<div v-if="i==1" class="icon big" :class="chatPageMsg.OverheadChat ? 'big-on' : 'big-off'" :ref="'big'+i"  name="on">
								<div class="smoll" :class="chatPageMsg.OverheadChat ? 'smoll-on' : 'smoll-off'" :ref="'smoll'+i"></div>
								<div class="mask"  @click.stop="offOrOn(i)"></div>
							</div>
							<!--消息免打扰啊-->
							<div v-else-if="i==2" class="icon big" :class="chatPageMsg.NoDisturbing ? 'big-on' : 'big-off'" :ref="'big'+i"  name="on">
								<div class="smoll" :class="chatPageMsg.NoDisturbing ? 'smoll-on' : 'smoll-off'" :ref="'smoll'+i"></div>
								<div class="mask"  @click.stop="offOrOn(i)"></div>
							</div>
							<!--强提醒-->
							<div v-else-if="i==3" class="icon big" :class="chatPageMsg.ForcedToRemind ? 'big-on' : 'big-off'" :ref="'big'+i"  name="on">
								<div class="smoll" :class="chatPageMsg.ForcedToRemind ? 'smoll-on' : 'smoll-off'" :ref="'smoll'+i"></div>
								<div class="mask"  @click.stop="offOrOn(i)"></div>
							</div>

							<i v-else class="icon icon-chevron-right"></i>
							<p  v-if="i==3">
								<span style="color: gray;" v-if="show">将全屏提醒“{{new Date().toLocaleTimeString()}}”之后的消息</span>
							</p>
						</div>
					</div>

					<!--查找聊天记录页面-->
					<transition name="rightToLeft">
						<div class="chat Page" v-if="chat">
							<div class="chatpagehead">
								<i @click="goback" class="chatpagereturn icon-chevron-left"></i>
								<input @input="seachValues" type="text" placeholder="搜索" class="input" autofocus>
							</div>

							<div style="margin-top: 3rem;" v-if="seachresult.length > 0" @click="tomsg">
								<a v-for="(v,i) in seachresult" :key="i" class="item" :href="'#' + v.id + 'i'">
									<div class="img-div">
										<img class="img" :src="v.self ? $store.state.myInfomation.headPortrait : chatPageMsg.headPortrait" alt="">
									</div>
									<div class="text">
										<p class="name">{{v.self ? $store.state.myInfomation.name ? $store.state.myInfomation.name : $store.state.myInfomation.nickname : chatPageMsg.name ? chatPageMsg.name : chatPageMsg.nickname}}</p>
										<p class="msg"><span v-for="(va,i) in msgsplit(v.msg)" :key="i" :class="{'active':va.color}">{{va.text}}</span></p>
									</div>
									<div class="time">
										{{time(v.data)}}
									</div>
								</a>
							</div>

						</div>
					</transition>

					<!--确认删除页面-->
					<transition name="opacity">
						<div v-show="showDeleteInfomation" class="DeleteInfomation Page" @click="showDeleteInfomation = false">
							<div class="box" @click.stop="">
								<div class="text">
									确定删除和 “{{chatPageMsg.name ? chatPageMsg.name : chatPageMsg.nickname}}” 的聊天记录吗？
								</div>
								<div class="button">
									<span class="cancel" @click="showDeleteInfomation = false">取消</span>
									<span class="affirm" @click="deleteInfomation">清空</span>
								</div>
							</div>
						</div>
					</transition>

					<!--选择聊天背景页面-->
					<transition name="rightToLeft">
						<div class="ChatPageMore Page" v-show="setBg">
							<div class="chatpagehead">
								<i @click="setBg = false" class="chatpagereturn icon-chevron-left"></i>
								<span class="chatpagename">聊天背景</span>
							</div>

							<div style="margin-top: 3rem;">
								<div class="item">
									<div class="text" @click="choicebg = true">选择聊天背景图</div>
									<i class="icon icon-chevron-right"></i>
								</div>
								<div class="item">
									<div class="text">从相册中选择</div>
									<i class="icon icon-chevron-right"></i>
								</div>
								<div class="item">
									<div class="text">拍一张</div>
									<i class="icon icon-chevron-right"></i>
								</div>
							</div>


							<!--选择聊天背景-->
							<transition name="rightToLeft">
								<div class="ChatPageMore Page" v-show="choicebg">
									<div class="chatpagehead">
										<i @click="choicebg = false" class="chatpagereturn icon-chevron-left"></i>
										<span class="chatpagename">选择聊天背景</span>
									</div>

									<div style="margin-top: 3rem;">
										<div @click="changeBg(v)" v-for="(v,i) in $store.state.headList" :key="i" :class="{'aaa': (i - 1) % 3 == 0}"
										style="border-radius: 1rem; position: relative;width:7rem;height: 7rem;display: inline-block;margin-top:0.5rem; margin-bottom: 0.5rem">
											<img :src="v" alt="不支持"
											style="width: 100%;height: 100%;border-radius: 1rem;">
											<div v-show="chatPageMsg.ChatBackground == v" style="position: absolute;bottom: 0;font-size: 2rem;
											height: 1.5rem;background: rgba(0,250,154,0.8);width: 100%;
											text-align: center;border-bottom-left-radius: 1rem;border-bottom-right-radius: 1rem">√</div>
										</div>
									</div>
								</div>
							</transition>

						</div>
					</transition>

				</div>
			</transition>
			
		</div>
	</keep-alive>
</template>

<script>
	export default {
		data() {
			return {
                styleObject: {
                    background: this.$store.state.chatPageMsg.ChatBackground + '!important',
                },
				havevalue: false,
				chatPageMsg: this.$store.state.chatPageMsg,
				show: false,
                setBg:false, //设置背景页面
                choicebg:false,//选择背景页面
                chat:false,//查找聊天记录页面
				seachValue:null,//查找值
				seachresult:[],//查询结果类表
				showDeleteInfomation: false,
				// bg:`background-img:url(${require('../../public/img/' + ${this.chatPageMsg.ChatBackground} + '.png'))`,
				ChatPageMore: [
					{text:"查找聊天记录",icon:"icon-chevron-right",class:"top"},
					{text:"顶置聊天",icon:"icon-chevron-right",class:"top border"},
					{text:"消息免打扰",icon:"icon-chevron-right",class:"border"},
					{text:"强提醒",icon:"icon-chevron-right",class:"border"},
					{text:"设置当前聊天背景",icon:"icon-chevron-right",class:"top"},
					{text:"清空聊天记录",icon:"icon-chevron-right",class:"top"},
					{text:"投诉",icon:"icon-chevron-right",class:"top bottom"},
				],
			};
		},
		created(){
			this.chatPageMsg = this.$store.state.chatPageMsg;
		},
		mounted(){
			console.log(this.$refs.all)
			this.$refs.all.style.backgroundImage = "url(../../public/img/20.png)!important"
		},
		methods: {
			judgevalue(e){
				e.target.value ? this.havevalue = true :  this.havevalue = false;
			},
			// 显示详情页面
			showInfoPage(){
				this.$store.commit('showInFoPage',true);//显示InFoPage页面
			},
			sendmsg(){
				let msg = this.$refs.chatpageinput.value;
				this.$refs.chatpageinput.value = "";
				this.havevalue = false;
				this.$refs.chatpageinput.focus();
				
				let data = new Date().toLocaleDateString()
				let a = new Date().toLocaleTimeString();
				let b = a.split(":");
				let time = b[0]+ ':' + b[1];
				let id = this.chatPageMsg.informationlength;
				console.log(a,time)
				
				let msgObj = {id,"read":true,msg,"self":true,data,time}
				//read:消息是否已读
				//msg:聊天信息
				//self:是否是我发的
				//data:日期
				//time:准确时间
				this.$store.commit('sendMsg',[this.chatPageMsg.weChatId,msgObj,{id,"read":false,msg,"self":false,data,time}]);
				console.log('生成的消息',msgObj)
			},
			offOrOn(i){
				if(this.$refs['big' + i][0].getAttribute('name') === 'on'){
					if(i===3){//消息强提醒
						this.show = true;
                        this.$store.commit('ForcedToRemind',[this.chatPageMsg.weChatId,true])
					}else if(i==2){//消息免打扰
						this.$store.commit('NoDisturbing',[this.chatPageMsg.weChatId,true])
					}else if(i == 1){
                        this.$store.commit('OverheadChat',[this.chatPageMsg.weChatId,true]);
					}
					this.$refs['big' + i][0].setAttribute('name',"off")
				}else{
					if(i==3){
						this.show = false;
                        this.$store.commit('ForcedToRemind',[this.chatPageMsg.weChatId,false])
					}else if(i == 2){
						this.$store.commit('NoDisturbing',[this.chatPageMsg.weChatId,false])
					}else if(i == 1){
                        this.$store.commit('OverheadChat',[this.chatPageMsg.weChatId,false])
					}
					this.$refs['big' + i][0].setAttribute('name',"on")
				}
				
			},
			allFun(i){
				console.log(i)
				if(i == 0){//查找聊天记录
				    this.chat = true;
				}else if(i == 4){//设置聊天背景
				    this.setBg = true;
				}else if(i === 5){
					this.showDeleteInfomation = true//显示删除聊天记录页面
				}
			},
			deleteInfomation(){
				this.$store.commit('information',this.chatPageMsg.weChatId);//删除聊天信息
				this.showDeleteInfomation = false;//隐藏删除聊天记录页面
			},
            changeBg(v){
			    this.$store.commit('changebg',[this.chatPageMsg.weChatId,v]);
                this.setBg = false;
                this.$store.commit('showOrHidden',['chatPageMore',false]);
				this.choicebg = false;
			    console.log('改变背景',this.chatPageMsg.weChatId,v);
			},
			//查找聊天记录
            seachValues(e){
                this.seachValue = e.target.value;
			    if(e.target.value == ' ' || e.target.value.length == 0){this.seachresult = [];return}
			    console.log('查找聊天记录',e.target.value == ' ',e.target.value.length)
                this.seachresult = [];
                this.$store.state.chatPageMsg.information.map(v => {
                    if(v.msg.match(e.target.value)){this.seachresult.push(v)}
				})
			},
			//分割时间
            time(t) {
              	let a = t.split('/');
              	return a[1] + "月" + a[2] + '日';
			},
			//分割消息
            msgsplit(v){
			    let a = v.split(this.seachValue);
				let b = [];
			    a.map(v=>{
			        if(v.length == 0){
			            b.push({text:this.seachValue,color:true});
					}else{
			            b.push({text:v,color:false})
					}
				})
			    return b
			},
			//返回
			goback(){
                this.seachresult = [];
                this.chat = false;
			},
            tomsg(){
			    console.log('去消息发送位置');
                this.seachresult = [];
                this.$store.commit('showOrHidden',['chatPageMore',false]);
                this.chat = false;
			}
		}
	}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>


	.chatpagehead{
		display: flex;
		position: fixed;
		background: rgb(150,160,170);
		width: 100%;
		height: 3rem;
		z-index: 20;
	}
	
	.chatpagereturn,.contactsmore{
		display: inline-block;
		width: 3rem;
		line-height: 3rem;
		font-size: 3rem;
	}
	.chatpagename{
		flex: 1;
		line-height: 3rem;
		text-align: left;
		padding: 0 1rem;
	}
	.contactsmore{
		transform: translateY(-0.4rem);
	}

	.messageitem{
		position: relative;
		width: 100%;
		min-height: 3rem;
		margin-top: 1rem;
		
		.left,.right{
			position: absolute;
			display: inline-block;
			width: 0;
			height: 0;
			top: 1rem;
			border-color: transparent;
			border-style: solid;
			border-right-color: rgba(255,255,255,0.6);
		}
		.left{
			left: -0.64rem;
			border-width: 0.5rem 0.7rem 0.5rem 0rem;
		}
		
		.right{
			right: -0.64rem;
			border-left-color: rgba(255,255,255,0.6);
			border-width: 0.5rem 0 0.5rem 0.7rem;
		}
	}
	
	.headImg{
		display: inline-block;
		width: 3rem;
		margin: 0 0.7rem;
		height: 3rem;
	}
	.headImg .auto-img{
		background: white;
		border-radius: 0.5rem;
	}
	.onemsg{
		position: relative;
		text-align: center;
		min-height: 1.6rem;
		line-height: 1.6rem;
		display: inline-block;
		vertical-align: top;
		max-width: 50%;
		min-width: 10%;
		padding: 0.7rem 0.5rem;
		border-radius: 0.5rem;
		background: rgba(255,255,255,0.6);
		text-align: left;
	}
	.chatpage{
		width: 100%;
		height: 100%;
		// margin-top: 24px;
		position: fixed;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		z-index: 20;
		background: rgb(240,230,220);
	}
	
	.chatpage .all{
		width: 100%;
		height: 100%;
		position: absolute;
		/*background-image: url(https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=2735633715,2749454924&fm=27&gp=0.jpg)!important;*/
		background-repeat: no-repeat;
		background-size:cover;
		bottom: 0;
	}
	
	.information{
		position: absolute;
		background-size: cover!important;
		background-repeat: no-repeat!important;
		height: calc(100% - 6rem);
		overflow: scroll;
		top: 3rem;
		width: 100%;
	}
	
	.information::-webkit-scrollbar{
		display: none;
	}
	
	.information .self{
		text-align: right;
	}
	
	.information .other{
		text-align: left;
	}
	
	.chatpagemore,.record,.face{
		width: 2.5rem;
		margin: 0 0.2rem;
		display: inline-block;
		line-height: 2rem;
		font-size: 2rem;
		vertical-align: top;
	}
	
	.chatpageinput{
		border-radius: 0.4rem;
		padding: 0 0.5rem;
		flex: 1;
		outline: none;
		border: none;
		background: white;
		height: 2rem;
	}
	
	.chatpagesend{
		width: 2.5rem;
		background: rgb(0,255,0);
		border-radius: 0.3rem;
		line-height: 2rem;
		margin: 0 0.2rem;
		
	}
	
	.chatpagekeyboard{
		position: absolute;
		bottom: 0;
		right: 0;
		left: 0;
		background: rgb(240,240,240);
		z-index: 20;
	}
	
	.chatpage .keyboard{
		width: 100%;
		height: 15rem;
	}
	
	.v-enter,.v-leave-to{
		left: 100%;
	}
	
	.v-enter-active{
		transition: all 0.5s linear;
	}
	
	.v-enter-to{
		left: 0;
	}
	
	
	
	.ChatPageMore{
		position: fixed;
		// top: 24px;
		bottom: 0;
		right: 0;
		left: 0;
		width: 100%;
		z-index: 22;
		background: rgb(240,230,220);
		
		.body{
			margin-top: 3.5rem;
		}
		
		.imgDiv{
			background: white;
			display: flex;
			height: 5.5rem;
			margin-top: 0.5rem;
			padding: 1rem 0.5rem 0.5rem 1rem;
			
			.imgItem{
				flex: 0 0 4rem;
				margin-right: 1rem;
			}
			.auto-img{
				height: 3.5rem;
				width: 3.5rem;
			}
			.name{
				font-size: 0.8rem;
				color: gray;
				line-height: 2rem;
				height: 2rem;
				padding-left: 0.2rem;
				width: 3.5rem;
				text-overflow: ellipsis;
				overflow: hidden;
				white-space: nowrap;
			}
			
			.addFriend{
				position: relative;
				display: inline-block;
				line-height: 3.5rem;
				width: 3.5rem;
				height: 3.5rem;
				text-align: center;
				font-size: 8rem;
				box-sizing: border-box;
				border: 1px solid gray;
				
				.vertical,.horizontal{
					position: absolute;
					display: inline-block;
					width: 2.5rem;
					height: 0.2rem;
					background: gray;
					top: calc(50% - 0.1rem);
					left: 0.5rem;
					transform: rotateZ(0deg);
				}
				
				.horizontal{
					transform: rotateZ(90deg);
				}
			}
		}
	}
</style>

<!-- 聊天信息页面 -->
<style lang="scss" rel="stylesheet/scss" scoped>
	.ChatPageMore{
		
	}
	.top{
		margin-top: 0.8rem;
	}
	.border{
		border-bottom: 1px solid rgb(55,55,55);
	}
	.bottom{
		margin-bottom: 3rem;
	}
	
	.item{
		position: relative;
		padding: 0 1rem;
		line-height: 4rem;
		background: white;

		.icon{
			position: absolute;top: 0;
			height: 4rem;
			line-height: 4rem;
			flex: 0 0 3.5rem;
			text-align: right; 
			right: 1rem;
		}

		.big-on{
			background: springgreen!important;
		}

		.big-off{
			background: gray;
		}

		.big{
			position: absolute;
			top: 1rem;
			right: 1rem;
			flex: 0 0 3.5rem;
			width: 3.5rem;
			height: 1.9rem;
			padding: 0.2rem;
			box-sizing: border-box;
			background: gray;
			border-radius: 1rem;
			
			.smoll{
				position: relative;
				height: 1.5rem;
				width: 1.5rem;
				border-radius: 50%;
				background: white;
				transition: all 0.2s linear;
			}

			.smoll-on{
				left: 1.5rem;
			}
			.smoll-off{
				left: 0rem;
			}
			
			.mask{
				position: absolute;
				top: 0;
				left: 0;
				z-index: 2;
				height: 1.9rem;
				width: 100%;
			}
		}
	}
	
	
