<template>
	<div class="SetNotesAndLabels Page">
		<div class="SetNotesAndLabelshead">
			<i class="icon setgoback icon-chevron-left" @click="$store.commit('showOrHidden',['setNotesAndLabelsPage',false])"></i>
			<span class="settext">设置备注和标签</span>
			<span v-if="setFinish" class="setFinishdone" @click="sendsetmsg">完成</span>
			<span v-else class="setFinish">完成</span>
		</div>
		
		<div class="setsetmsg">
			<div>
				<p>备注名</p>
				<input ref="name" type="text" :value="infomsg.name? infomsg.name : infomsg.nickname" @input="setinput">
			</div>
			<div>
				<p>标签</p>
				<input ref="label" @input="setinput" v-if="infomsg.label" :value="infomsg.label" >
				<input ref="label" @input="setinput" v-else placeholder="添加标签对联系人进行分类" type="text">
			</div>
			<div>
				<p>电话号码</p>
				<div class="border-bottom">
					<div  v-if="infomsg.phoneNumber">
						<input class="phoneInput" v-for="(v,i) in infomsg.phoneNumber" :key="i" @input="setinput" :value="v" type="search">
						<input class="phoneInput" :key="infomsg.phoneNumber.length" type="search" @input="setinput" placeholder="添加电话号码">
					</div>
					<input class="phoneInput" @input="setinput" v-else placeholder="请添加电话号码" type="search">
				</div>
			</div>
			<div>
				<p>描述</p>
				<div  class="border-bottom">
					<input ref="describe" v-if="infomsg.describe" :value="infomsg.describe" type="text" @input="setinput">
					<input ref="describe" v-else placeholder="添加更多备注信息" @input="setinput">
					<span >400</span>
				</div>
			</div>
			<div>
				<p>附加图片</p>
				<input ref="picture" v-if="infomsg.picture" :value="infomsg.picture" type="file">
				<input ref="picture" v-else placeholder="添加照片或相关图片"  type="file">
			</div>
		</div>
		
	</div>
</template>

<script>
	export default {
		data() {
			return {
				setFinish:false,
				setmsg:{},
				infomsg: null,
			};
		},
		created(){
			this.infomsg = this.$store.state.chatPageMsg;
		},
		methods: {
			sendsetmsg(){
				let phoneNumber = []
				let phoneInputs = document.getElementsByClassName('phoneInput');
				console.log(phoneInputs)
				for(var i=0; i<phoneInputs.length; i++){
					console.log(i,phoneInputs[i].value)
					if(phoneInputs[i].value){phoneNumber.push(phoneInputs[i].value);}
				}
				// if(this.$refs.phoneNumber.value){phoneNumber.push(this.$refs.phoneNumber.value)}
				
				let setinfomation = {
					weChatId:this.infomsg.weChatId,
					name:this.$refs.name.value,
					label:this.$refs.label.value.split(' '),
					phoneNumber: phoneNumber,
					describe:this.$refs.describe.value.split(' '),
					picture:this.$refs.picture.value
				}
				this.$store.commit('setNotes',setinfomation);
				this.$store.commit('showOrHidden',["setNotesAndLabelsPage",false])//隐藏当页面
			},
			setinput(e){
				console.log('调用了',e.target.value);
				//判断更改后的值与原始值是否相同;
				this.setFinish = true;//input框由输入内容时，设置完成按钮为可点击
			},
		}
	}
</script>

<style>
	.SetNotesAndLabels{
		z-index: 21;
	}
	.SetNotesAndLabelshead{
		position: sticky;
		top: 0;
		width: 100%;
		display: flex;
		height: 3rem;
		z-index: 2;
		border-bottom: 1px solid rgba(17,27,37,0.1);
		background: rgb(237,237,237);
	}
	
	.setgoback{
		flex: 0 0 3rem;
		height: 3rem;
		line-height: 3rem;
	}
	
	.settext{
		flex: 1;
		height: 3rem;
		line-height: 3rem;
	}
	
	.setFinish,.setFinishdone{
		flex: 0 0 4rem;
		height: 2rem;
		line-height: 2rem;
		background: rgba(12,240,12,.3);
		margin: 0.5rem .5rem;
		border-radius: 0.2rem;
		text-align: center;
	}
	.setFinishdone{
		background: rgb(12,240,12);
	}
	.setsetmsg>div{
		margin-top: 1rem;
	}
	.setsetmsg{
		text-align: left;
		padding: 1rem 1rem 5rem 1rem;
	}
	
	.setsetmsg input{
		display: inline-block;
		height: 3rem;
		width: 18rem;
		line-height: 3rem;
		border: none;
		outline: none;
		padding: 0 0.5rem;
		background: whitesmoke;
		font-size: 1.7rem;
		caret-color: rgb(12,245,12)
	}
	.setsetmsg p{
		margin: 1rem 0;
	}
	.setsetmsg p{
		font-size: 1.5rem;
		color: darkgray;
	}
	.border-bottom{
		border-bottom:  1px solid rgb(17,27,37,0.1);
	}
</style>
