<template>
	<div class="list">
		<!--顶置列表-->
		<div class="WeChatListItem overhead-chat" v-for="(v,i) in OverheadChatList" :key="i" :ref="i + 'i'" @touchstart="showChatPage(v,i,i+'i',$event)" @touchend="touchend(v,i,OverheadChatList.length + i+'i',$event)">
			<div class="headPortrait">
				<img class="auto-img"  :src=" v.headPortrait">
				<!-- <img class="auto-img"  :src="require('../../public/img/' + v.headPortrait + '.png')"> -->
				<div class="init">
					<div class="NoDisturbingDiv" v-if="unreadNumber(v.information) > 0 && v.NoDisturbing">
						<span class="NoDisturbingRed"></span>
					</div>
					<div class="unreadNumberDiv"  v-if="unreadNumber(v.information) > 0 && !v.NoDisturbing">
						<span class="unreadNumber">{{unreadNumber(v.information) > 0 ? unreadNumber(v.information) : ''}}</span>
					</div>
				</div>
			</div>
			
			<div class="text">
				<div class="name">
					<p class="nickname" v-html="v.name ? v.name : v.nickname"></p>
					<p class="lastmsg">{{unreadNumber(v.information) > 0 && v.NoDisturbing ? '[' + unreadNumber(v.information) + '条] ' : ''}}{{v.information[v.information.length-1].self ? '我' : v.name || v.nickname}} : {{v.information[v.information.length-1].msg}}</p>
				</div>
				<div class="time">
					<p class="times"><code>{{v.information[v.information.length-1].time}}</code></p>
					<p class="NoDisturbing" v-if="v.NoDisturbing"><i class="icon-bell-off"></i></p>
				</div>
			</div>
		</div>

		<!--普通列表-->
		<div class="WeChatListItem" v-for="(v,i) in unOverheadChatList" :key="OverheadChatList.length + i + 'i'" :ref="OverheadChatList.length + i+'i'" @touchstart="showChatPage(v,i,OverheadChatList.length + i+'i',$event)" @touchend="touchend(v,i,i+'i',$event)">
			<div class="headPortrait">
				<img class="auto-img"  :src=" v.headPortrait">
				<!-- <img class="auto-img"  :src="require('../../public/img/' + v.headPortrait + '.png')"> -->
				<div class="init">
					<div class="NoDisturbingDiv" v-if="unreadNumber(v.information) > 0 && v.NoDisturbing">
						<span class="NoDisturbingRed"></span>
					</div>
					<div class="unreadNumberDiv"  v-if="unreadNumber(v.information) > 0 && !v.NoDisturbing">
						<span class="unreadNumber">{{unreadNumber(v.information) > 0 ? unreadNumber(v.information) : ''}}</span>
					</div>
				</div>
			</div>

			<div class="text">
				<div class="name">
					<p class="nickname" v-html="v.name ? v.name : v.nickname"></p>
					<p class="lastmsg">{{unreadNumber(v.information) > 0 && v.NoDisturbing ? '[' + unreadNumber(v.information) + '条] ' : ''}}{{v.information[v.information.length-1].self ? '我' : v.name || v.nickname}} : {{v.information[v.information.length-1].msg}}</p>
				</div>
				<div class="time">
					<p class="times"><code>{{v.information[v.information.length-1].time}}</code></p>
					<p class="NoDisturbing" v-if="v.NoDisturbing"><i class="icon-bell-off"></i></p>
				</div>
			</div>
		</div>

		<!--聊天页面-->
		<transition name="rightToLeft">
			<ChatPage v-if="$store.state.chatPage"></ChatPage>
		</transition>

		<!--长按选择框-->
		<div class="page" v-show="longTapChioce" @click="hiddenchoice">
			<div class="boxi" ref="box" @click.stop="" :style="[{'left':left + 'px'},{'top':top + 'px'}]">
				<div>标为未读</div>
				<div @click="OverheadChat">{{curren && curren.OverheadChat ? "取消顶置" : "顶置聊天"}}</div>
				<div @click = 'deletecurren'>删除该聊天</div>
			</div>
		</div>

	</div>
</template>

<script>
	import ChatPage from './ChatPage.vue'
	export default {
		data() {
			return {
			    timer:null,
				time:0,//记录按下时长
				index:0,//记录当前下标值
                longTapChioce:false,//长按显示框
				left:null,
				top:null,
                refItem:null,//保存选中的联系人元素
                curren:null,//保存选中的联系人
			}
		},
		computed:{
            OverheadChatList(){
                // console.log(this.$store.getters.msg)
                return this.$store.getters.msg.filter(v=>{
                   return v.OverheadChat
                })
			} ,
            unOverheadChatList(){
                return this.$store.getters.msg.filter(v=>{
                    return !v.OverheadChat
                })
            }
        },
		methods:{
			unreadNumber(v){
				let total = 0;
				for(let i = v.length-1; i>=0;i--){
					if(v[i].read){break}
					else{total++}
				}
				return total
			},
			showChatPage(v,i,ref,e){
			    this.curren = v;//更新当前点击的值
				this.refItem = this.$refs[ref][0];//获取当前点击元素
                this.refItem.style.background = 'lightgrey';//点击改变背景
				this.timer = setInterval(()=>{//设置定时器
				    this.time = this.time + 1;
				    if(this.time >= 10){//当按下1秒后
                        this.left = e.touches[0].pageX >= window.screen.width / 2 ? e.touches[0].pageX - 119 : e.touches[0].pageX;//点击位置位于页面右边，将框显示在左边，否则显示在右边
                        this.top = e.touches[0].pageY > window.screen.height / 2 ? e.touches[0].pageY - 126 : e.touches[0].pageY;//点击位置位于页面上方，将框显示在下方，否则显示在上方
                        this.longTapChioce = true;//显示选择框页面
                        clearInterval(this.timer);//清除定时器
					}
				},100)

			},
            hiddenchoice(){
                this.longTapChioce = false;
                this.refItem.style.background = 'rgb(255,255,255)';//恢复背景
            },
            touchend(v){
                clearInterval(this.timer);
                if(this.time >= 10){
                    this.time = 0;
                }else {
                    this.refItem.style.background = 'rgb(255,255,255)';//恢复背景
                    this.$store.commit('reloadMsg',v.weChatId);//更新当前联系人信息
                    this.$store.commit('showOrHidden',["chatPage",true]);//显示聊天页面
				}

			},
            OverheadChat(){
                this.longTapChioce = false;
                this.refItem.style.background = 'rgb(255,255,255)';//恢复背景
			    if(this.curren && this.curren.OverheadChat){//如果已经顶置，取消顶置
                    this.$store.commit('OverheadChat',[this.curren.weChatId,false]);
                }else {//否则顶置
                    this.$store.commit('OverheadChat',[this.curren.weChatId,true]);
				}
			},
            deletecurren(){
                this.longTapChioce = false;
				this.$store.commit('information',this.curren.weChatId)
			}
		},
		components:{
			ChatPage
		}
	}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
	.overhead-chat{
		background: lightgrey!important;
	}
	
	.list{
		padding-bottom: 1rem;
	}

	.active{
		background: gray;
	}
	.WeChatListItem{
		display: flex;
		height: 4rem;
		background: rgb(255,255,255);
		
		.headPortrait{
			position: relative;
			flex: 0 0 3rem;
			margin: 0.5rem 1rem;
			height: 3rem;
			margin-right: 1rem;
			
			.auto-img{
				
				background: rgba(200,200,200,0.5);
			}
		}
		
		.text{
			flex: 1;
			display: flex;
			box-sizing: border-box;
			padding-bottom: 0;
			border-bottom: 1px solid rgb(242,242,242);
			
			.name{
				flex: 1;
				
				.nickname{
					height: 1.7rem;
					line-height: 1.7rem;
					font-size: 1.7rem;
					width: 12rem;
					overflow: hidden;
					text-overflow: ellipsis;
					white-space: nowrap;
				}
				
				.lastmsg{
					width: 12rem;
					overflow: hidden;
					text-overflow: ellipsis;
					white-space: nowrap;
					height: 1.2rem;
					line-height: 1.2rem;
					color: rgb(161,161,161);
					margin-bottom: 0.6rem;
				}
			}
			
			.time{
				flex: 0 0 4rem;
				padding-right: 1rem;
				text-align: right;

				.times{
					height: 2rem;
					line-height: 2rem;
				}
				
				.NoDisturbing{
					height: 1.5rem;
				}
			}
		}
	}
	
	.init{
		position: absolute;
		top: -0.4rem;
		right: -0.4rem;
		
		.unreadNumberDiv{
			background: red;
			height: 1.5rem;
			min-width: 1rem;
			display: inline-block;
			border-radius: 0.6rem;
			display: inline-block;
			
			.unreadNumber{
				position: relative;
				top: 0.4rem;
				width: 100%;
				display: inline-block;
				text-align: center;
				color: white;
				font-size: 0.8rem;
			}
		}
		
		
		.NoDisturbingDiv{
			position: absolute;
			top: 0rem;
			right: 0.1rem;
			
			
			.NoDisturbingRed{
				display: inline-block;
				width: 0.7rem;
				height: 0.7rem;
				border-radius: 50%;
				background: red;
			}
		}
		
	}
</style>


<style lang="scss" rel="stylesheet/scss" scoped>
	.page{
		position: fixed;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		background: rgba(200,200,200,0.5);
		z-index: 25;

		.boxi{
			position: absolute;

			& div{
				background: #fff;
				height: 3rem;
				line-height: 3rem;
				width: 7rem;
				padding-left: 1.5rem;
			}
		}
	}

</style>

