<template>
	<div class="infopage">
		<div class="chatpagehead">
			<!-- 隐藏InFoPage页面 -->
			<i @click="$store.commit('showOrHidden',['inFoPage',false])" class="chatpagereturn icon-chevron-left"></i>
			<!-- 显示Infopagemore页面 -->
			<span class="contactsmore"  @click="$store.commit('showOrHidden',['infopagemorePage',true])">...</span>
		</div>
		
		<div class="infoPageHeadMsg">
			<div class="headMsgImg">
				<!-- <img class="auto-img" :src="require('../../public/img/' + infoPageMsg.headPortrait + '.png')"> -->
				<img class="auto-img" :src="infoPageMsg.headPortrait">
			</div>
			<div class="infodiv">
				<div class="nameOrNickname">
					{{infoPageMsg.name ? infoPageMsg.name : infoPageMsg.nickname}} 
					<i style="display: inline-block;width: 1rem;height: 1rem;color: blue;" :class="infoPageMsg.sex == 'man' ? 'icon-user-tie' : 'icon-woman'"></i>
				</div>
				<p class="nickname smallText" v-if="infoPageMsg.name">昵称：{{infoPageMsg.nickname}}</p>
				<p class="wechatId smallText">微信号：{{infoPageMsg.weChatId}}</p>
				<p class="region smallText">地区：{{infoPageMsg.region}}</p>
			</div>
			<div class="theStar">
				<i style="color: yellow;" class="icon-star-full" v-show="infoPageMsg.theStar"></i>
			</div>
		</div>

		<div v-if="infoPageMsg.phoneNumber.length>0 || infoPageMsg.label.length > 0 || infoPageMsg.describe.length > 0">
			<div class="phoneNumber " @click="$store.commit('showOrHidden',['setNotesAndLabelsPage',true])" v-if="infoPageMsg.phoneNumber.length>0">
				<div class="remark" style="flex: 0 0 5rem;padding-right: 0;">电话号码</div>
				
				<div style="flex: 1;">
					<div class="remark" style="padding-left: 0;" v-for="(v,i) in infoPageMsg.phoneNumber" :key="i" @click.stop="">{{v}}</div>
				</div>
			</div>
			
			<div class="remark flex"  v-if="infoPageMsg.label" @click="$store.commit('showOrHidden',['setNotesAndLabelsPage',true])">
				<span class="sometext">标签</span>
				<div class=" info">
					<span class="lebel" v-for="(v,i) in infoPageMsg.label" :key="i">{{v}}</span>
				</div>
				<i class="infopageicon">></i>
			</div>
			
			<div class="remark flex"  v-if="infoPageMsg.describe" @click="$store.commit('showOrHidden',['setNotesAndLabelsPage',true])">
				<span class="sometext">描述</span>
				<div class="info">
					<span class="describe" v-for="(v,i) in infoPageMsg.describe" :key="i">{{v}}</span>
				</div>
				<i class="infopageicon">></i>
			</div>
		</div>
		
		<div v-else class="remark setremark flex" @click="$store.commit('showOrHidden',['setNotesAndLabelsPage',true])">
			<span style="flex: 1;font-weight: 600;">设置备注和标签</span>
			<i class="infopageicon">></i>
		</div>
		
		<div class="friendandmoreinfo">
			<div class="remark flex" style="border-bottom: 1px solid rgb(17,27,34,0.1);" @click="$store.commit('showOrHidden',['circleOfFriendsPage',true])">
				<span class="sometext">朋友圈</span> 
				<div class="info">
					<span  v-for="(v,i) in infoPageMsg.CircleOfFriends" :key="i">
						<img class="auto-img" v-if="v.photograph[0] && i < 5" :src="v.photograph[0]">
					</span>
				</div>
				<i class="infopageicon" >></i>
			</div>
			<div class="remark flex" @click="$store.commit('showOrHidden',['infopagemorePage',true])">
				<span class="sometext">更多信息 </span>
				<div class="info"></div>
				<i class="infopageicon" >></i>
			</div>
		</div>
			
		<div class="infopagevoice" style="margin-bottom: 2rem;">
			<div class="infosend remark"  @click="showChatPage">
				<i class="icon icon-bubble2"></i>
				<span class="text">发送消息</span>
			</div>
			<div class="remark radio">
				<i class="icon icon-video"></i>
				<span class="text">音视频通话</span>
			</div>
		</div>
		
		<!-- 设置页面 -->
		<!-- 隐藏 Infopagemore 页面-->
		<transition name="bottomToTop">
			<div v-if="$store.state.infopagemorePage" class="infopagemore" @click="$store.commit('showOrHidden',['infopagemorePage',false])">
				<div class="setmore" @click.stop="" >
					<div class="setMoreItem" v-for="(v,i) in infopagemoreList" :key="i" @click="v.fun(v.v)">
						<i class="icon" :class="v.icon"></i>
						<span class="text">{{v.text}}</span>
					</div>
				</div>
			</div>
		</transition>
		
		<!-- 设置标星成功页面 -->
		<transition name='star'>
			<div v-show="setTheStarSucceed" class="setTheStarSucceed">
				已{{infoPageMsg.theStar ? '设为标星朋友' : '取消标星朋友'}}
			</div>
		</transition>
		
		<!-- 确认是否删除页面 -->
		<transition name="opacity">
			<div class="deletePage" v-if="$store.state.deletePage" @click.stop="">
				<div class="box">
					<h2 class="hint">删 除 联 系 人</h2>
					<p class="info">
						将联系人“{{infoPageMsg.name ? infoPageMsg.name : infoPageMsg.nickname}}”删除，
						将会同时删除与该联系人的聊天记录
					</p>
					<p class="button">
						<span class="cancel" @click="$store.commit('showOrHidden',['deletePage',false])">取消</span>
						<span class="suer" @click="deleteAddr(infoPageMsg.weChatId)">删除</span>
					</p>
				</div>
			</div>
		</transition>
		
		<transition-group name="rightToLeft">
			<!-- 朋友圈页面 -->
			<CircleOfFriendsPage :infoPageMsg="infoPageMsg" v-if="$store.state.circleOfFriendsPage" key="CircleOfFriendsPage"></CircleOfFriendsPage>
			<!-- 是指标签和备注页面 -->
			<setNotesAndLabelsPage v-if="$store.state.setNotesAndLabelsPage" key="SetNotesAndLabels"></setNotesAndLabelsPage>
		</transition-group>
	</div>
</template>

<script>
	import CircleOfFriendsPage from './CircleOfFriendsPage.vue'
	import setNotesAndLabelsPage from './setNotesAndLabelsPage.vue'
	export default {
		data() {
			return {
				showinfopagemore: false,
				setTheStarSucceed: false,
				infoPageMsg: null,
				infopagemoreList: [
					{fun:this.fun,v:'SetNotesAndLabels',icon:"icon-add_circle",text:"设置备注和标签"},
					{fun:this.fun,v:'',icon:"icon-share",text:"把他推荐给朋友"},
					{fun:this.fun,v:'setTheStar',icon:"icon-star-full",text:this.$store.state.chatPageMsg.theStar ? '取消标星朋友' : '设为标星朋友'},
					{fun:this.fun,v:'',icon:"icon-aperture-alt",text:"设置朋友圈和视频动态权限"},
					{fun:this.fun,v:'',icon:"icon-cancel",text:"加入黑名单"},
					{fun:this.fun,v:'',icon:"icon-warning",text:"投诉"},
					{fun:this.fun,v:'',icon:"icon-add_circle",text:"添加到页面"},
					{fun:this.fun,v:'showDelete',icon:"icon-add_circle",text:"删除"},
				]
			};
		},
		// props:['infoPageMsg'],
		created(){
			this.infoPageMsg = this.$store.state.chatPageMsg
		},
		methods: {
			showChatPage(){
				this.$store.commit('showOrHidden',["chatPage",true])//显示聊天页面
				this.$store.commit('showOrHidden',["inFoPage",false])//隐藏详情页面
				// this.$store.commit('showChatPage',true);
				// this.$store.commit('showInFoPage');
			},
			deleteAddr(weChatId){
				this.$store.commit('deleteAdder',weChatId);//删除当前联系人
				this.$store.commit('showOrHidden',['deletePage',false]);//隐藏删除页面
				// this.$store.commit('showInFoPage',false);
				this.$store.commit('showOrHidden',['inFoPage',false])//隐藏InFoPage页面，回到了联系人页面，当前联系人被删除
			},
			fun(v){
				if(v == 'showDelete'){//删除联系人
					this.$store.commit('showOrHidden',['infopagemorePage',false]);//隐藏Infopagemore页面
					// this.$store.commit('showDelete',true);
					this.$store.commit('showOrHidden',['deletePage',true]);//显示是否删除页面
					
				}else if(v == "SetNotesAndLabels"){
					this.$store.commit('showOrHidden',['infopagemorePage',false]);//隐藏Infopagemore页面
					this.$store.commit('showOrHidden',['setNotesAndLabelsPage',true])////显示SetNotesAndLabels页面
					
				}else if(v == "setTheStar"){//标星朋友
				
					this.$store.commit('setTheStar',this.infoPageMsg.weChatId);//设置标星
					this.infopagemoreList[2].text = this.infopagemoreList[2].text == "取消标星朋友" ? '设为标星朋友' : "取消标星朋友"
					this.$store.commit('showOrHidden',['infopagemorePage',false]);//隐藏Infopagemore页面
					this.setTheStarSucceed = !this.setTheStarSucceed;
					setTimeout(()=>{this.setTheStarSucceed = !this.setTheStarSucceed;},1500)
				}
			}
		},
		components:{
			CircleOfFriendsPage,
			setNotesAndLabelsPage
		}
	}
</script>

<!-- 删除联系人页面 -->
<style lang="scss" rel="stylesheet/scss" scoped>
	.deletePage{
		position: fixed;
		top: 0;
		bottom: 0;
		right: 0;
		left: 0;
		width: 100%;
		z-index: 21;
		background: rgba(123,123,123,0.2)!important;
		
		.box{
			position: absolute;
			top: 50%;
			left: 5%;
			transform: translateY(-50%);
			width: 90%;
			background: white !important;
			border-radius: 0.5rem;
			
			.hint{
				height: 5rem;
				line-height: 5rem;
				text-align: center;
				font-size: 1.7rem;
				font-weight: 700;
			}
			
			.info{
				padding: 0 1.5rem;
				line-height: 1.7rem;
				font-size: 1.5rem;
			}
			
			.button{
				text-align: center;
				display: flex;
				margin-top: 2.8rem;
				height: 4rem;
				line-height: 4rem;
				border-top: 1px solid rgba(155,155,155,0.5);
				
				.cancel{
					flex: 1;
					font-weight: 700;
					font-size: 1.6rem;
					border-right: 1px solid rgba(155,155,155,0.5);
				}
				
				.suer{
					flex: 1;
					font-weight: 700;
					font-size: 1.6rem;
					color: red;
				}
			}
		}
	}
</style>
<!-- 设置标星页面 -->
<style lang="scss" rel="stylesheet/scss" scoped>
	.star-enter{
		top: 40%!important;
		opacity: 0;
	}
	.star-enter-active,.star-leave-active{
		transition: all 0.4s linear!important;
	}
	.star-enter-to,.star-leave{
		top: 50%!important;
		opacity: 1;
	}
	.star-leave-to{
		top: 60%!important;
		opacity: 0;
	}
	
	.setTheStarSucceed{
		position: fixed;
		top: 50%;
		left: 50%;
		width: 8rem;
		text-align: center;
		background: rgba(66,66,66,0.7)!important;
		color: white;
		border-radius: 0.5rem;
		padding: 1rem;
		font-weight: 700;
		transform: translate(-50%,-50%);
	}
</style>
<!-- 设置更多页面 -->
<style lang="scss" rel="stylesheet/scss" scoped>
	.infopagemore{
		padding-top: 3rem;
		width: 100%;
		height: 100%;
		// margin-top: 24px;
		position: fixed;
		box-sizing: border-box;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		z-index: 50;
		overflow: scroll;
		background: rgba(110,110,110,0.4)!important;
		
		.setmore{
			position: absolute;
			height: 28.7rem;
			overflow: scroll;
			bottom: 0;
			width: 100%;
		}
		.setmore::-webkit-scrollbar{
			display: none;
		}
	}
	
	.setMoreItem{
		display: flex;
		height: 4.4rem;
		line-height: 4.4rem;
		color: gray;
		
		.icon{
			line-height: 4.4rem;
			flex: 0 0 5rem;
			font-size: 2rem;
			text-align: center;
		}
		
		.text{
			flex: 1;
		}
	}
</style>
<!-- 总页面 -->
<style lang="scss" rel="stylesheet/scss" scoped>
	.infopage{
		padding-top: 3rem;
		width: 100%;
		height: 100%;
		// margin-top: 24px;
		position: fixed;
		box-sizing: border-box;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		z-index: 50;
		overflow: scroll;
		background: rgb(240,230,220);
		
		div{
			background: whitesmoke;
		}
	}
	
	.infopage::-webkit-scrollbar{
		display: none;
	}
	
	.chatpagehead{
		position: fixed;
		top: 0px;
		background: rgb(150,160,170);
		width: 100%;
		height: 3rem;
		z-index: 20;
		
		.chatpagereturn,.contactsmore{
			display: inline-block;
			width: 3rem;
			line-height: 3rem;
			font-size: 3rem;
		}
		
		.contactsmore{
			float: right;
		}
	}
	
	.infoPageHeadMsg{
		display: flex;
		padding: 1rem 0;
		
		.headMsgImg{
			width: 4rem;
			height: 4rem;
			margin: 0 1rem 0 1rem;
			
			.auto-img{
				background: white;
				border-radius: 0.5rem;
			}
		}
		
		.infodiv{
			flex: 1;
			.nameOrNickname{
				height: 2.5rem;
				line-height: 2.5rem;
				font-size: 2rem;
				font-weight: 800;
			}
			.smallText{
				font-weight: 100;
				color: gray;
				height: 1.5rem;
				line-height: 1.5rem;
				font-size: 1rem;
			}
		}
		.theStar{
			flex: 0 0 2rem;
			margin-right: 1.5rem;
			text-align: right;
		}
	}
	
	.phoneNumber{
		display: flex;
		border-top: 1px solid rgba(17,27,34,0.1);
		border-bottom: 1px solid rgba(17,27,34,0.1);
	}
	
	.remark{
		height: 2rem;
		padding: 1rem;
		line-height: 2rem;
	}
	
	.flex{
		display: flex;
		
		.sometext{
			flex: 0 0 5rem;
			font-weight: 600;
		}
		
		.info{
			font-weight: 300;
			flex: 1;
			
			.auto-img{
				transform: translateY(-0.5rem);
				margin-right: 0.5rem;
				width: 3rem;
				height: 3rem;
			}
			
			.lebel,.describe{
				display: inline-block;
				margin-right: 0.5rem;
				border-radius: 0.3rem;
				border: 1px solid #0099FF;
				padding: 0 0.3rem;
			}
			
			.describe{
				border: 1px solid #56A00E
			}
		}
		
		.infopageicon{
			flex: 0 0 1rem;
		}
	}

	.friendandmoreinfo,.infopagevoice{
		margin-top: 1rem;
	}
	
	.infopagevoice{
		text-align: center;
		
		.radio{
			border-bottom: 1px solid rgba(17,27,34,0.1);
			border-top: 1px solid rgba(17,27,34,0.1);
		}
		
		.icon{
			display: inline-block;
			margin-right: 1rem;
			font-size: 2rem;
			line-height: 2rem;
		}
		
		.text{
			font-weight: 700;
			color: #000088;
		}
	}
	
	
</style>
