<template>
	<div class="RightAddPage" @click="$store.commit('showOrHidden',['rightAddPage',false])">
		<div class="box" @click.stop="">
			<div class="angle"></div>
			
			<div class="item" v-for="(v,i) in data" @click="fun(v.v)" :key="i">
				<span class="icon"><i :class="v.icon"></i></span>
				<span class="text">{{v.text}}</span>
			</div>
		</div>
		
				
	</div>
</template>

<script>
	export default {
		data() {
			return {
				data:[
					{text:"发起群聊",icon:"icon-video",v:""},
					{text:"添加朋友",icon:"icon-video",v:"addFriend"},
					{text:"扫一扫",icon:"icon-video",v:""},
					{text:"收付款",icon:"icon-video",v:""},
					{text:"帮助与反馈",icon:"icon-video",v:""},
				],
				
			};
		},
		methods: {
			fun(v){
				if(v == "addFriend"){//显示添加朋友页面
					this.$store.commit('showOrHidden',['rightAddPage',false])//隐藏add页面
					this.$store.commit('showOrHidden',['addFriendPage',true])//显示添加好友页面
				}
			},
		}
	}
</script>

<!-- add页面 -->
<style lang="scss" rel="stylesheet/scss" scoped>
	.RightAddPage{
		position: fixed;
		top: 24px;
		bottom: 0;
		right: 0;
		left: 0;
		background: rgba(222,222,222,0.2);
		z-index: 10;
		
		.box{
			position: absolute;
			right: 0.8rem;
			top: 3.6rem;
			width: 11.2rem;
			background: rgb(85,85,85)!important;
			border-radius: 0.3rem;
			color: white;
			
			.angle{
				position: absolute;
				top: -0.8rem;
				right: 0.8rem;
				display: inline-block;
				width: 0;
				height: 0;
				border-width: 0.1rem 0.7rem 0.9rem 0.7rem;
				border-color: transparent;
				border-style: solid;
				border-bottom-color: rgb(85,85,85);
				
			}
		}
	}
	
	.item{
		display: flex;
		height: 3.5rem;
		line-height: 3.5rem;
		
		.icon{
			text-align: center;
			flex: 0 0 4rem;
			font-size: 2rem;
		}
		.text{
			flex: 1;
			box-sizing: border-box;
			border-bottom: 1px solid rgba(121,121,121,0.4);
		}
	}
</style>


