		//按照登录信息，找到对应的人赋值给myInfomation；
		login(state,[account,password]){
			if(!account)return  //实验用的原始数据
			for(let i=0; i<state.data.length; i++){//遍历data
			console.log(state.data[i],account)
				if(state.data[i].weChatId == account && state.data[i].myInfomation.password == password){//找到账号和密码都符合的用户
					state.myInfomation = state.data[i].myInfomation;//查找到当前用户的个人信息赋值给state.myInfomation，用于生产我的页面
					state.WeChat = state.data[i].friend;//将朋友列表赋值给WeChat，生成联系人列表
					state.loginList.push(state.data[i]);//向当前登录的账号中添加一个，在切换账号中使用
				}
			}
			console.log('登录成功',account,password)
		},
		
		//切换账号
		changeAccount(state,weChatId){
			console.log('切换账号',weChatId)
			for(let i=0; i<state.data.length; i++){
				if(state.data[i].weChatId == weChatId){ //在data列表中找到相同的wechaid 
					state.myInfomation = state.data[i].myInfomation;//自己的信息赋值给state.myInfomatio
					
					state.WeChat = state.data[i].friend;//好友类表赋值给state.WeChat 
					console.log('找到了',state.data[i].friend)
				}
			}
		},
		
		// 设置nickname
		setNickname(state,[weChatId,v]){
			console.log(weChatId,v,'设置nickname');
			state.myInfomation.nickname = v;//把当前用户的昵称更改
			state.data.map(value => {
				console.log('一层遍历找到每一个用户',value)
                value.friend.map(j => {
                    if(j.weChatId == weChatId){
                    	j.nickname = v;
                        console.log('二层遍历找到当前用户',j)
                    }
				})
			})
		},
		
		//设置备注和标签等信息
		setNotes(state,v){
			let a = state.WeChat.find( b => b.weChatId == v.weChatId);
			a.describe = v.describe;
			a.label =  v.label;
			a.name = v.name;
			a.phoneNumber = v.phoneNumber;
			console.log("修改备注和标签完毕",v)
		},
		
		//消息免打扰
