<template>
	<div class="Page">
		<!-- 登录页面 -->
			<div class="imgdiv">
				<img class="autoimg" src="../../public/img/25.png" alt="">
			</div>
			<div class="item">
				<span class="text">账号</span>
				<input type="text" class="account" ref="account" @input="check">
			</div>
			<div class="item">
				<span class="text">密码</span>
				<input type="text" class="password" ref="password" @input="check">
			</div>
			<div style="color:red" v-show="init">错误提示{{init}}</div>
			<div class="login" v-if="ok" :class="{'active':ok}" @click="login">登录</div>
			<div class="login" v-else>登录</div>
			<div class="login active" @click="register">注册</div>

		<!-- 注册页面 -->
		<transition name="rightToLeft">
			<Register v-if="$store.state.registerPage"></Register>
		</transition>
	</div>
</template>

<script>
	import Register from './Register.vue'

	export default {
		data() {
			return {
			    init:false,
				ok: false
			};
		},
		methods: {
			check(){
				if(this.$refs.account.value.length > 0 && this.$refs.password.value.length > 0){
					this.ok = true
				}else{
					this.ok = false
				}
			},
			login(){
                let data = this.$store.state.data;
                for(var i=0; i<data.length; i++){
                    console.log(data[i],this.$refs.account.value,this.$refs.password.value);
                    if(data[i].weChatId == this.$refs.account.value && data[i].myInfomation.password == this.$refs.password.value){
                        this.$store.commit('login',[this.$refs.account.value,this.$refs.password.value]);//提交登录信息
                        this.$store.commit('showOrHidden',['loginPage',false])//隐藏登录界面
                        this.$store.commit('showOrHidden',['allPage',true])//显示微信页面
                       	this.init = false
                        return
                    }
                }
				this.init = '账号或密码错误';
			},
			register(){
				this.$store.commit('showOrHidden',["registerPage",true]);//显示注册页面
			}
		},
		components: {
			Register
		}
	}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
	.imgdiv{
		height: 40%;
	}
	.autoimg{
		position: absolute;
		height: 6rem;
		width: 6rem;
		top: 15%;
		left: 50%;
		transform: translateX(-50%);
	}
	
	.item{
		height: 4rem;
		line-height: 4rem;
		border-bottom: 1px solid rgb(0,150,0);
		margin: 0 1rem;
		text-align: left;
		display: flex;
		
		.text{
			flex: 0 0 4rem;
		}
		
		.account,.password{
			flex: 1;
			height: 2rem;
			line-height: 2rem;
			margin-top: 1rem;
			border: none;
			background: rgb(237,237,237);
			outline: none;
			caret-color:green;
			caret-width:0.2rem;
			text-indent: 0.2rem;
		}
	}
	.login{
		width: calc(100% - 2rem);
		height: 3rem;
		background: rgba(0,150,0,0.5);
		margin-top: 2rem;
		margin-left: 1rem;
		border-radius: 0.5rem;
		font-size: 2rem;
		line-height: 3rem;
		color: rgba(200,200,200,0.5);
	}
	.active{
		background: rgb(0,150,0);
		color: rgb(200,200,200);
	}
</style>
