<template>
	<div>
		<!-- 添加好友页面 -->
		<transition name="rightToLeft">
			<div class="addFriendPage Page" v-if="$store.state.addFriendPage">
				<div class="chatpagehead">
					<i @click="goback" class="chatpagereturn icon-chevron-left"></i>
					<span class="chatpagename">添加朋友</span>
				</div>
				
				<div class="seach">
					<i class="icon-search seachIcon"></i>
					<input class="input" ref="input" @input="input" v-model="value" placeholder="微信号/QQ号/手机号">
				</div>
				<div class="item" v-show="ok" @click="searchUse" style="border-top: 1px solid rgba(55,55,55,0.2);">
					<div class="iconimg icon-qrcode" style="background: green;"></div>
					<div class="text" style="line-height: 5rem;font-size: 1.7rem;">搜索:<span style="color: green;">{{value}}</span></div>
				</div>
				
				<div class="weChatId">我的微信号{{$store.state.myInfomation.wechatId}}&nbsp;<i class="icon-qrcode"></i></div>
				
				<div>
					<div class="item" v-for="(v,i) in addFriendLidt" :key="i">
						<div class="iconimg" :class="v.icon" :style="v.bg"></div>
						<div class="text">
							<p class="big">{{v.text}}</p>
							<p class="smoll">{{v.textsmoll}}</p>
						</div>
						<div class="icon icon-chevron-right"></div>
					</div>
				</div>
				
				<!-- 搜索到好友页面 -->
				<transition name="rightToLeft" v-if="userPage">
					<div class="Page">
						<div class="chatpagehead">
							<i class="chatpagereturn icon-chevron-left" @click="userPage = false"></i>
						</div>
						
						<!-- 搜索用户不存在时 -->
						<div v-if="user.length == 1">
							<div class="infoText">
								{{user[0]}}
							</div>
						</div>
						
						<!-- 已经是好友时 -->
						<div v-else-if="user.length == 2">
							<div class="infoText">
								{{user[0]}}
							</div>
							<div class="addressItem" @click="showInFoPage(user[1].weChatId)">
								<img class="auto-img" :src="user[1].headPortrait">
								<span class="addressText" v-html="user[1].name ? user[1].name :user[1].nickname"></span>
							</div>
							
						</div>
						
						<!-- 搜索到用户，并不是好友时 -->
						<div v-else>
							<div class="headimg">
								<div class="imgdiv">
									<img class="img" :src="user.myInfomation.headPortrait"></img>
								</div>
								<div class="text">
									<div class="nickname">
										{{user.myInfomation.nickname}}
										<i style="display: inline-block;width: 1rem;height: 1rem;color: blue;" :class="user.myInfomation.sex == 'man' ? 'icon-user-tie' : 'icon-woman'"></i>
									</div>
									<div class="region">地区：{{user.myInfomation.region}}</div>
								</div>
							</div>
							
							<div class="items" style="display: flex;">
								<span style="font-weight: 600;font-size: 1.6rem;flex: 1rem;">设置备注和标签</span>
								<span style="display: inline-block;flex: 0 0 1rem; height: 3.5rem;line-height: 3.5rem;text-align: center;">
									<i class="icon-chevron-right" ></i>
								</span>
							</div>
							
							<div class="items">
								<span style="margin-right: 2rem;font-weight: 600;font-size: 1.6rem;">来源</span>
								<span style="color: gray;">来自手机号搜索</span>
							</div>
							
							<div class="items add" @click="addToaddress">添加到通讯录</div>
						</div>
					</div>
				</transition>
			</div>
		</transition>
		
		

	</div>
</template>

<script>
	export default {
		data() {
			return {
				value: null,
				ok: false,
				user: null,
				userPage: false,
				addFriendLidt: [
					{icon:"icon-qrcode",bg:"background:blue;",text:"雷达加朋友",textsmoll:"添加身边的朋友",v:""},
					{icon:"icon-qrcode",bg:"background: mediumspringgreen;",text:"面对面建群",textsmoll:"与身边的朋友进入同一个群聊",v:""},
					{icon:"icon-qrcode",bg:"background: blue;",text:"扫一扫",textsmoll:"扫描二维码名片",v:""},
					{icon:"icon-qrcode",bg:"background: orangered;",text:"手机联系人",textsmoll:"添加或邀请通讯录中的朋友",v:""},
					{icon:"icon-qrcode",bg:"background: mediumblue;",text:"公众号",textsmoll:"获取更多资讯和服务",v:""},
					{icon:"icon-qrcode",bg:"background: midnightblue;",text:"企业微信联系人",textsmoll:"通过手机号搜索企业微信用户",v:""}
				]
			};
		},
		methods: {
			showInFoPage(v){
				console.log('查找用户中更新联系人',v)
				this.$store.commit('reloadMsg',v);//更新当前联系人信息
				this.$store.commit('showOrHidden',['inFoPage',true])//显示InFoPage页面
				this.userPage = false;//隐藏查找到好友页面
				this.goback();
			},
			goback(){
				this.value = '';
				this.ok = false;
				this.$store.commit('showOrHidden',['addFriendPage',false]);//隐藏添加好友页面
			},
			input(e){
				if(e.target.value.length > 0){
					this.ok = true
				}else{
					this.ok = false
				}
			},
			searchUse(){
				this.$store.commit("searchUse",this.value);//提交信息，查找用户
				this.user = this.$store.state.searchUse;//获取查找到的用户的信息，赋值给当前user
				this.userPage = true;
				// console.log(this.$store.state.searchUse);
				// console.log('搜索用户：',this.$refs.input.value)
			},
			addToaddress(){
				this.$store.commit("addToaddress",this.user.weChatId);
				this.userPage = false;
			}
		}
	}
</script>

<!-- 添加朋友页面 -->
<style lang="scss" rel="stylesheet/scss" scoped>
	.chatpagehead{
		position: sticky;
		top: 0;
		display: flex;
		background: rgb(150,160,170);
		width: 100%;
		height: 3rem;
		z-index: 1;
		
		.chatpagereturn,.contactsmore{
			display: inline-block;
			flex: 0 0 3rem;
			line-height: 3rem;
			font-size: 3rem;
		}
		.chatpagename{
			flex: 1;
			line-height: 3rem;
			text-align: left;
		}
	}

	.seach{
		display: flex;
		height: 4rem;
		line-height: 4rem;
		background: white;
		
		.seachIcon{
			flex: 0 0 4rem;
			display: inline-block;
			heihgt:4rem;
			color: gray;
			line-height: 4rem;
			text-align: center;
			font-size: 2rem;
		}
		
		.input{
			border: none;
			outline: none;
			font-size: 1.8rem;
		}
	}
	
	.weChatId{
		height: 2rem;
		margin-top: 1rem;
		text-align: center;
		font-size: 1.5rem;
		color: gray;
		margin-bottom: 2rem;
	}
	
	.item{
		display: flex;
		height: 5rem;
		background: white;
		padding: 0 1rem;
		
		.iconimg{
			flex: 0 0 3rem;
			height: 3rem;
			margin: 1rem 1rem 1rem 0;
			font-size: 2rem;
			line-height: 3rem;
			text-align: center;
		}
		
		.icon{
			flex: 0 0 1rem;
			height: 5rem;
			line-height: 5rem;
			color: gray;
		}
		
		.text{
			flex: 1;
			border-bottom: 1px solid rgba(55,55,55,0.2);
			
			.big{
				color: black;
				height: 2.5rem;
				line-height: 3rem;
				font-size: 2rem;
				font-weight: 700;
			}
			
			.smoll{
				color: gray;
				height: 2rem;
				line-height: 2rem;
				margin-bottom: 0.7rem;
			}
		}
	}
</style>

<!--  -->
<style lang="scss" rel="stylesheet/scss" scoped>
	.infoText{
		height: 4rem;
		background: white;
		text-align: center;
		font-size: 2rem;
		font-weight: 700;
		line-height: 4rem;
		margin-bottom: 0.5rem;
	}
	
	.addressItem{
		background: rgb(255,255,255);
		display: flex;
		
		.auto-img{
			flex: 0 0 4rem;
			width: 4rem;
			height: 4rem;
			border-radius: 0.5rem;
			margin-left: 1rem;
		}
		
		.addressText{
			flex: 1;
			line-height: 4rem;
			font-size: 2rem;
			height: 4rem;
			border-bottom: 1px solid rgba(55,55,55,0.5);
		}
		
	}
	
	.headimg{
		height: 7rem;
		display: flex;
		background: white;
		padding: 1rem 1rem 2rem 1rem;
		box-sizing: border-box;
		
		.imgdiv{
			flex:0 0 5rem;
			
			.img{
				width: 4rem;
				height: 4rem;
				margin-right: 1rem;
				border-radius: 0.5rem;
			}
		}
		
		.text{
			flex: 1;
			
			.nickname{
				height: 2.5rem;
				line-height: 2.5rem;
				font-size: 2rem;
				font-weight: 700;
			}
			
			.region{
				height: 1.5rem;
				line-height: 1.5rem;
			}
		}
	}
	
	.items{
		height: 3.5rem;
		background: white;
		line-height: 3.5rem;
		margin-bottom: 0.5rem;
		padding: 0 1rem;
	}
	
	.add{
		text-align: center!important;
		font-size: 1.6rem;
		font-weight: 600;
		color: royalblue;
	}
</style>


