<template>
	<div class="MainPage">
		<i class="camera icon-camera"></i><!-- 相机 -->
		<div class="head" @click="$store.commit('showOrHidden',['mainInfoPage',true])">
			<div class="img" >
				<!-- <img class="auto-img" :src="require('../../public/img/' + mySmg.headPortrait + '.png')"> -->
				<img class="auto-img" :src="mySmg.headPortrait">
			</div>
			<div class="text">
				<p class="nickname">{{mySmg.nickname}}</p>
				<p class="wechatId">微信号:{{mySmg.weChatId}}</p>
			</div>
			<div class="iconGroup">
				<p class="icon">&nbsp; </p>
				<p class="icon">
					<i class="icon-qrcode"></i><!-- 二维码 -->
					<i class="icon-chevron-right"></i>
				</p>
			</div>
		</div>
		
		<div>
			<div class="item" v-for="(v,i) in faxianList" :key="i" @click.stop="text(v.v)">
				<div class="icon-left"><i class="icon" :class="v.icon"></i></div>
				<div class="itemText">
					<span class="text">{{v.text}}</span>
					<i class="icon icon-right icon-chevron-right"></i>
				</div>
			</div>
		</div>
		
		<!-- 个人信息页面 -->
		<transition name="rightToLeft">
			<div class="MainInfoPage Page" v-if="$store.state.mainInfoPage">
				<div class="chatpagehead">
					<i @click="$store.commit('showOrHidden',['mainInfoPage',false]);" class="chatpagereturn icon-chevron-left"></i>
					<span class="chatpagename">个人信息</span>
				</div>
				
				
				<div class="item" v-for="(v,i) in mySmgList" :key="i" :class="{'headImg' : i==0}" @click="fun(v.v)">
					<!-- <div v-if="i==1"></div> -->
					<div class="text">{{v.text}}</div>
					<div class="icon">
						<!-- <img class="auto-img" v-if="i==0" :src="require('../../public/img/' + mySmg.headPortrait + '.png')" alt=""> -->
						<img class="auto-img" v-if="i==0" :src="mySmg.headPortrait" alt="">
						<span v-if="i == 1" class="nickname">{{mySmg.nickname}}</span>
						<span v-if="i == 2">{{mySmg.weChatId}}</span>
						<i v-if="i == 3" class="icon-qrcode icon-right"></i>
						&nbsp;
						<i v-if="i == 2"></i>
						<i class="icon-chevron-right" v-else></i>
					</div>
				</div>
			</div>
		</transition>
	
		<!-- 更改名字页面 -->
		<transition name="rightToLeft">
			<div class="Page changNamPage" v-if="$store.state.changNamPage">
				<div class="chatpagehead">
					<i @click="$store.commit('showOrHidden',['changNamPage',false])" class="chatpagereturn icon-chevron-left"></i>
					<span class="chatpagename">更改名字</span>
					<span @click="setNickname" :class="ok?'active' : 'normal'">保存</span>
				</div>
				
				<div class="imputNickname">
					<input @input="input" ref="setNickname" autofocus="true" v-model="nickname" class="nicknameInput">
					<p style="line-height: 1rem;color: gray;">{{mySmg.nickname}}</p>
				</div>
			</div>
		</transition>
	
		<!-- 设置页面 -->
		<transition name="rightToLeft">
			<div v-if="$store.state.setPage" class="Page">
				<!-- 头部 -->
				<div class="chatpagehead">
					<i @click="$store.commit('showOrHidden',['setPage',false]);" class="chatpagereturn icon-chevron-left"></i>
					<span class="chatpagename">设置</span>
				</div>
				<!-- 主体 -->
				<div v-for="(v,i) in setList" :key="i" class="item" :class="v.class" @click="set(v.v,v.text)">
					<div class="text">{{v.text}}</div>
					<div class="icon"><i class="icon-chevron-right"></i></div>
				</div>
				
				<!-- 切换账号页面 -->
				<transition name="rightToLeft">
					<div class="Page switchoverPage" v-if="$store.state.switchoverPage">
						<div class="chatpagehead">
							<i @click="$store.commit('showOrHidden',['switchoverPage',false]);" class="chatpagereturn icon-chevron-left"></i>
						</div>
						<div class="weixinimg">
							<img class="img" src="../../public/img/25.png">
							<p class="img-text">轻触头像以切换账号</p>
						</div>

						<div @click="changCount(v.weChatId)" class="imgHead" v-for="(v,i) in $store.state.loginList" :key="i">
							<img class="img" :src="v.myInfomation.headPortrait" alt="">
							<div class="text">{{v.myInfomation.nickname}}</div>
						</div>
						
						<div class="imgItem imgHead" @click="closeWechat">
							<span class="addFriend">
								<i class="vertical"></i><i class="horizontal"></i>
							</span>
							<div class="text">切换账号</div>
						</div>
						
					</div>
				</transition>
				
				<!-- 退出微信页面 -->
				<transition name="bottomToTop">
					<div v-if="$store.state.closePage" class="close Page" @click="loseWechat">
						<div class="box" @click.stop="closeWechat" >
							<div class="flex">
								<i class="icon"></i>
								<span class="text">退出登录</span>
							</div><div class="flex">
								<i class="icon"></i>
								<span class="text">关闭微信</span>
							</div>
						</div>
					</div>
				</transition>
				
			</div>
		</transition>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				ok: false,
				mySmg: null,
                nickname:null,
				faxianList:[
					{icon:"",v:"",text:"支付"},
					{icon:"icon-box",v:"",text:"收藏"},
					{icon:"icon-images",v:"",text:"相册"},
					{icon:"icon-folder-open",v:"",text:"卡包"},
					{icon:"icon-grin",v:"",text:"表情"},
					{icon:"icon-settings",v:"set",text:"设置"}
				],
				mySmgList: [
					{text:"头像",v:""},
					{text:"昵称",v:"nickname"},
					{text:"微信号",v:""},
					{text:"二维码名片",v:""},
					{text:"更多",v:""},
					{text:"我的地址",v:""},
				],
				setList: [
					{text:"账号与安全",v:"",class:""},
					{text:"新消息提醒",v:"",class:"mt"},
					{text:"勿扰模式",v:"",class:""},
					{text:"聊天",v:"",class:""},
					{text:"隐私",v:"",class:""},
					{text:"通用",v:"",class:""},
					{text:"关于微信",v:"",class:"mt"},
					{text:"帮助与反馈",v:"",class:""},
					{text:"插件",v:"",class:"mt"},
					{text:"切换账号",v:"switchover",class:"mt"},
					{text:"退出登录",v:"exit",class:""},
				]
			};
		},
		created(){
			this.mySmg = this.$store.state.myInfomation;
			this.nickname = this.mySmg.nickname
		},
		methods: {
			closeWechat(){
				this.$store.commit('showOrHidden',['allPage',false]);//隐藏所有页面
				this.$store.commit('showOrHidden',['closePage',false]);//隐藏关闭微信页面
				this.$store.commit('showOrHidden',['setPage',false]);//隐藏设置页面
				this.$store.commit('showOrHidden',['loginPage',true])//显示登录页面
				this.$store.commit('saveData');//关闭页面时，将数据保存在cookies中
			},
			// 更换联系人
			changCount(id){
				this.$store.commit('changeAccount',id);//改变当前登录人信息
				this.$store.commit('showOrHidden',['switchoverPage',false]);//隐藏更换联系人页面
				this.$store.commit('showOrHidden',['setPage',false]);//隐藏设置页面
				this.mySmg = this.$store.state.myInfomation;//更新当前页面信息
				this.$store.dispatch('hiddenAndShow')
			},
			set(v,t){
				console.log(v,t)
				if(v == 'exit'){
					this.$store.commit('showOrHidden',['closePage',true]);//显示退出微信页面
					// this.$store.commit('closePage',true)
// 					this.$store.commit('setPage',false);//隐藏设置页面
// 					this.$store.commit('allPage',false);//隐藏all页面
// 					this.$store.commit('loginPage',true);//显示登录页面
				}else if(v == "switchover"){
					console.log(this.$store.state.loginList);
					this.$store.commit('showOrHidden',['switchoverPage',true]);//显示切换账号页面
					// this.$store.commit('switchover',true);//显示切换账号页面
				}
			},
			text(v){
				if(v == "set"){
					this.$store.commit('showOrHidden',['setPage',true]);
					// this.$store.commit('setPage',true);
				}
				console.log(v)
			},
			fun(v){
				console.log(v)
				if(v == "nickname"){
					this.$store.commit('showOrHidden',['changNamPage',true]);//显示更改名字页面
				}
			},
			setNickname(){
				console.log(this.$refs.setNickname.value)
				let v = this.$refs.setNickname.value;
				this.$store.commit('setNickname',[this.mySmg.weChatId,v]);//更改名字
				this.$store.commit('showOrHidden',['changNamPage',false]);//隐藏更改名字页面
			},
			input(e){
				console.log(e.target.value.length==this.mySmg.nickname.length)
				if(e.target.value.length==this.mySmg.nickname.length){
					console.log('没修改')
					this.ok = false;
				}else{
					console.log('修改了')
					this.ok = true;
				}
			},
			loseWechat(){
       			 this.$store.commit('saveData');//保存数据
               	 this.$store.commit('showOrHidden',['closePage',false]);
			}
		}
	}
</script>


<style lang="scss" rel="stylesheet/scss" scoped>
	.camera{
		position: absolute;
		top: 0.3rem;
		right: 1rem;
		// z-index: 2;
		font-size: 2rem;
		font-weight: 700;
	}
	.head{
		background: white;
		height: 8rem;
		display: flex;
		padding: 2rem 1rem;
		box-sizing: border-box;
		
		.img{
			flex: 0 0 5.5rem;
			
			.auto-img{
				width: 4rem;
				height: 4rem;
				border-radius: 0.5rem;
			}
		}
		
		.text{
			flex: 1;
			
			.nickname,.wechatId{
				width: 11rem;
				line-height: 2rem;
				text-overflow: ellipsis;
				white-space: nowrap;
				overflow: hidden;
			}
			.nickname{
				font-size: 2rem;
				font-weight: 700;
			}
		}
		
		.iconGroup{
			flex: 0 0 4rem;
			text-align: right;
			position: relative;
			.icon{
				line-height: 2rem;
				i{
					display: inline-block;
					width: 1.5rem;
					font-size: 2rem;
				}
			}
			
		}
	}
	.item{
		display: flex;
		width: calc(100% - 2rem);
		background: rgb(255,255,255)!important;
		padding: 0 1rem;
		.icon{
			background-image:-webkit-linear-gradient(bottom,red,#fd8403,yellow); 
			-webkit-background-clip:text; 
			-webkit-text-fill-color:transparent;
		}
		
		.icon-left{
			flex: 0 0 3rem;
			line-height: 3.8rem;
		}
		
		.itemText{
			flex: 1;
			display: flex;
			line-height: 3.8rem;
			
			.text{
				flex: 1;
			}
			
			.icon-right{
				flex: 0 0 1.5rem;
				line-height: 3.8rem;
			}
		}
	}
	
	.itemText{ 
		box-sizing: border-box;
		border-bottom: 1px solid rgba(157,157,157,0.5);
		box-sizing: border-box;
	}

	.item:first-child{
		margin: 0.5rem 0;
	}
	.item:last-child {
		margin: 0.5rem 0 2rem 0;
	}
	.item:first-child .itemText,
	.item:last-child .itemText,
	.item:nth-child(5) .itemText{
		border: none;
	}
</style>

<!-- 我的信息页面 -->
<style lang="scss" rel="stylesheet/scss" scoped>
	.chatpagehead{
		display: flex;
		background: rgb(150,160,170);
		width: 100%;
		height: 3rem;
		z-index: 1;
		
		.chatpagereturn,.contactsmore{
			display: inline-block;
			flex: 0 0 3rem;
			line-height: 3rem;
			font-size: 3rem;
		}
		.chatpagename{
			flex: 1;
			line-height: 3rem;
			text-align: left;
		}
	}
	
	.item{
		display: flex;
		min-height: 4rem;
		border-bottom: 1px solid rgba(157,157,157,0.5);
		line-height: 4rem;
		
		.text{
			flex: 0 0 6rem;
		}
		
		.icon{
			flex: 1;
			text-align: right;
		}
	}
	
	.headImg{
		height: 5rem;
		line-height: 5rem;
		.auto-img{
			width: 4rem;
			height: 4rem;
			margin: 0.5rem;
		}
		.icon-right{
			line-height: 5rem!important;
		}
	}
</style>

<!-- 更改姓名页面 -->
<style lang="scss" rel="stylesheet/scss" scoped>
	.imputNickname{
		padding: 1rem;
		line-height: 4rem;
	}
	
	.nicknameInput{
		height: 2rem;
		border: none;
		background: rgb(237,237,237);outline: none;
		border-bottom: 1px solid rgb(0,66,0);
		width: 100%;
		font-size: 2rem;
	}
	.active,.normal{
		display: inline-block;
		line-height: 2rem;
		width: 4rem;
		height: 2rem;
		background: rgba(0,150,0,0.5);
		margin: 0.5rem;
		text-align: center;
		border-radius: 0.3rem;
		color: rgba(200,200,200,0.5);
	}
	.active{
		background: rgb(0,150,0);
		color: white;
	}
</style>

<!-- 设置页面 -->
<style lang="scss" rel="stylesheet/scss" scoped>
	.chatpagehead{
		position: sticky;
		top: 0;
	}
	.mt{
		margin-top: 1rem;
	}
	
	.item:last-child{
		margin-top: 0!important;
		margin-bottom: 2rem!important;
	}
</style>

<!-- 切换账号页面 -->
<style lang="scss" rel="stylesheet/scss" scoped>
	.switchoverPage{
		
		.img{
			width: 3.5rem;
			height: 3.5rem;
		}
		.weixinimg{
			margin-top: 2rem;
			height: 10rem;
			text-align: center;
			
			.img-text{
				margin-top: 3rem;
				font-size: 2.5rem;
				font-weight: 700;
			}
		}
		
		.imgHead{
			width: 30%;
			display: inline-block;
			text-align: center;
			margin-top: 2rem;
			
			.text{
				width: 80%;
				margin: 1rem 10%;
				overflow: hidden;
				text-overflow: ellipsis;
				white-space: nowrap;
			}
		}
	}
	
	.addFriend{
		position: relative;
		display: inline-block;
		line-height: 3.5rem;
		width: 3.5rem;
		height: 3.5rem;
		text-align: center;
		font-size: 8rem;
		box-sizing: border-box;
		border: 1px solid gray;
		
		.vertical,.horizontal{
			position: absolute;
			display: inline-block;
			width: 2.5rem;
			height: 0.2rem;
			background: gray;
			top: calc(50% - 0.1rem);
			left: 0.5rem;
			transform: rotateZ(0deg);
		}
		
		.horizontal{
			transform: rotateZ(90deg);
		}
	}
</style>

<!-- //退出微信页面 -->
<style lang="scss" rel="stylesheet/scss" scoped>
	.close{
		background: rgba(10,10,10,0.5);
	}
	
	.box{
		position: absolute;
		bottom: 0;
		width: 100%;
		height: 8rem;
		background: white;
	}
	
	.flex{
		display: flex;
		height: 4rem;
		
		.icon{
			flex: 0 0 3rem;
		}
		.text{
			flex: 1;
			height: 4rem;
			line-height: 4rem;
		}
	}
	
</style>
