<template>
	<div class="addressList">
		<!-- 顶部列表 -->
		<div style="margin-bottom: 1rem;">
			<div class="addressItem" v-for="(v,i) in headList" :key="i" @click="v.fun(v.text,$event)">
				<div class="headImg">
					<div class="auto-img" :style="v.style">
						<i :class="v.icon"></i>
					</div>
				</div>
				<div class="addressText lastItem">{{v.text}}</div>
			</div>
		</div>
		
		<!-- 联系人列表 -->
		<div>
			<div class="addressItem" v-for="(v,i) in contactlist" :key="i" @click="showInFoPage(v)">
				<!-- <div class="headImg"><img class="auto-img" :src="require('../../public/img/' + v.headPortrait + '.png')"></div> -->
				<div class="headImg"><img class="auto-img" :src="v.headPortrait"></div>
				<div class="addressText" v-html="v.name ? v.name : v.nickname"></div>
			</div>
		</div>
		
		<!-- 详情页面 -->
		<transition name="rightleft">
			<InfoPage v-if="$store.state.inFoPage"></InfoPage>
		</transition>
	</div>
</template>

<script>
	import InfoPage from './InfoPage.vue'
	export default {
		data() {
			return {
				contactlist:'',
				// infoPageMsg: null,
				headList: [
					{icon:"icon icon-user-plus",style:"background: rgb(250 157 59)",text:"新朋友",fun:this.ceshi},
					{icon:"icon icon-users",style:"background: rgb(7 191 95)",text:"群聊",fun:this.ceshi},
					{icon:"icon icon-price-tag",style:"background: rgb(39 130 215)",text:"标签",fun:this.ceshi},
					{icon:"icon icon-user-tie",style:"background: rgb(39 130 215)",text:"公众号",fun:this.ceshi}
				]
				
			};
		},
		created(){
			this.contactlist = this.$store.state.WeChat;//获取数据，并赋值给this.data
		},
		methods: {
			showInFoPage(v){
				console.log('更新联系人信息',v,v.weChatId)
				this.$store.commit('reloadMsg',v.weChatId);//更新当前联系人信息
				this.$store.commit('showOrHidden',['inFoPage',true])//显示InFoPage页面
			},
			ceshi(v,e){
				console.log('测试成功',v,e)
			},
		},
		components: {
			InfoPage
		}
	}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
	.addressItem{
		display: flex;
		height: 3.6rem;
		padding-left: 1rem;
		background: rgb(255,255,255);
		
		.headImg{
			margin: 0.5rem 0;
			flex: 0 0 3.6rem;
			line-height: 2.6rem;
			height: 2.6rem;
			
			.auto-img{
				text-align: center;
				width: 2.6rem;
				background: gray;
			}
			
			.icon{
				line-height: 2.6rem;
				font-size: 2rem;

			}
		}
		
		.addressText{
			flex: 1;
			line-height: 3.6rem;
			height: 3.6rem;
			box-sizing: border-box;
			border-bottom: 1px solid #f2f2f2;
		}
		
		.lastItem{
			border: none;
		}
	}
</style>
